/* jshint ignore:start */

define('brandtalk-web/config/environment', ['ember'], function(Ember) {
  var prefix = 'brandtalk-web';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (runningTests) {
  require("brandtalk-web/tests/test-helper");
} else {
  require("brandtalk-web/app")["default"].create({"name":"brandtalk-web","version":"0.0.0.f6ee96cb"});
}

/* jshint ignore:end */
